mark {
  background-color: #867eba;
}

.modal {
  z-index: 50000;
  background-color: #0000;
  width: 100%;
  height: 100%;
  animation: .3s ease-out fadeIn;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-content {
  color: #0f0;
  white-space: nowrap;
  background-color: #1e1e1e;
  border: 1px solid #888;
  border-radius: 10px;
  width: 80%;
  max-width: 700px;
  margin: 10% auto;
  padding: 20px;
  font-family: Courier New, monospace;
  font-size: 18px;
  animation: .5s ease-out slideIn;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #0003;

  & #play-btn, & #pause-btn {
    cursor: pointer;
    font-size: 16px;
    display: none;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#terminalText {
  white-space: normal;
  word-wrap: break-word;
}

.cursor {
  vertical-align: middle;
  background-color: #3f0;
  width: 2px;
  height: 20px;
  margin-left: 3px;
  animation: .8s step-end infinite blink;
  display: inline-block;
}

@keyframes blink {
  50% {
    background-color: #0000;
  }
}

.close {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: -4px;
  right: 8px;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

#popupBtn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  box-shadow: 0 2px 5px #0003;
}

#popupBtn:hover {
  background-color: #45a049;
}

@keyframes flowingSand {
  0% {
    opacity: 1;
    filter: blur();
    transform: translateY(0)scale(1);
  }

  100% {
    opacity: 0;
    filter: blur(2px);
    transform: translateY(-50px)scale(1.2);
  }
}

.raindrop {
  background: #b4d7e2cc;
  border-radius: 50%;
  width: 2px;
  height: 15px;
  animation: linear forwards fall;
  position: absolute;
  top: -20px;
}

@keyframes fall {
  from {
    opacity: 1;
    transform: translateY(-20px);
  }

  to {
    opacity: .5;
    transform: translateY(95vh);
  }
}
